.calender {
    display: inherit;
    width: 80%;
    /* float: right; */
    height: 200px;
    border: solid;
    margin-top: 50px;
}

.events {
    line-height: 24px;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .events li {
    color: #999;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  
  .events li span {
    vertical-align: middle;
  }
  
  .events li span:first-child {
    font-size: 9px;
    margin-right: 4px;
  }
  
  .event-warning {
    color: #fac450;
  }
  
  .event-normal {
    color: #108ee9;
  }
  
  .event-error {
    color: #f50;
  }
  
  .notes-month {
    text-align: center;
  }
  .notes-month section {
    font-size: 28px;
  }